<template>
  <div class="centered">
    <h1>Complementos</h1>
    <div class="cake-box">
      <div class="container" v-for="complemento, index in complementos">
        <img :src="complemento.img_src" :alt="complemento.nombre" class="cake-image"/>
        <div class="nombre">{{ complemento.nombre }}</div>
        <div class="precio">$ {{ complemento.precio }}</div>
        <div class="descripcion">{{ complemento.descripcion }}</div>
        <button type="button" class="button" @click="agregar_complementos(index, complemento.nombre)">Agregar</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Complementos",
  computed: {
    complementos() {
      return this.$store.state.complementos;
    },
  },
  methods: {
    agregar_complementos: function(index, complemento){
      let payload = {
        index: index,
        nombre : complemento
      }
      this.$store.commit('agregar_complementos', payload)

    }
  }
};
</script>
