<template>
  <div id="Ordenar" class="tabcontent">
    <form id="pedido" action="pedido.js">
      <div v-if="$store.getters.orden_length == 0">
          <div class="alert alert-info h4 mt-3" role="alert">
            <i class="fa-solid fa-triangle-exclamation"></i>
            Es necesario agregar almenos un pastel para realizar el pedido.
          </div>
      </div>
      <div v-else>
        <h1>Realizar Pedido</h1>
        <h3 class="text-center">Datos Cliente</h3>
        <label for="nombre">Cual es tu nombre:</label>
        <input
          type="text"
          id="nombre"
          name="nombre"
          class="form-control mb-2"
          placeholder="nombre"
        />
        <label for="telefono">Cual es tu telefono:</label>
        <input
          type="number"
          id="telefono"
          name="telefono"
          class="form-control mb-2"
          placeholder="telefono"
        />
        <label for="email">Cual es tu correo electronico:</label>
        <input
          type="email"
          id="email"
          name="email"
          class="form-control mb-2"
          placeholder="email"
        />
        <h3 class="text-center">Detalle del pedido</h3>
        <div class="table-responsive">
          <table class='table table-condensed table-sm'>
            <thead>
              <tr>
                <th>Descripcion</th>
                <th>Cantidad</th>
                <th>Valor Unitario</th>
                <th>Importe</th>
              </tr>
            </thead>
            <tbody>
              <p class="text-success m-0 h5">Pasteles</p>
              <tr v-for="pastel in pasteles">
                <td>{{ pastel.nombre }}</td>
                <td>{{ pastel.cantidad }}</td>
                <td>{{ formatCurrency(pastel.precio) }}</td>
                <td>{{ formatCurrency(pastel.precio * pastel.cantidad) }}</td>
              </tr>
              <p class="text-success m-0 h5" v-if="$store.state.orden.adornos.length > 0">Adornos</p>
              <tr v-for="adorno in adornos">
                <td>{{ adorno.nombre }}</td>
                <td>{{ adorno.cantidad }}</td>
                <td>{{ formatCurrency(adorno.precio) }}</td>
                <td>{{ formatCurrency(adorno.precio * adorno.cantidad) }}</td>
              </tr>
              <p class="text-success m-0 h5" v-if="$store.state.orden.complementos.length > 0">Complementos</p>
              <tr v-for="complemento in complementos">
                <td>{{ complemento.nombre }}</td>
                <td>{{ complemento.cantidad }}</td>
                <td>{{ formatCurrency(complemento.precio) }}</td>
                <td>{{ formatCurrency(complemento.precio * complemento.cantidad) }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <h4 class="text-success text-center">Total: {{ formatCurrency(get_total) }} </h4>
        <br />
        <div id="buttons" class="text-center p-2">
          <button type="reset" class="button m-2">
            <span class="fa fa-arrow-alt-circle-left"></span>
            Reset
          </button>
          <button type="submit" class="button m-2">
            <span class="fa fa-cash-register"></span>
            Ordenar
          </button>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  name: "Formulario",
  computed: {
    pasteles() {
      return this.$store.state.orden.pasteles;
    },
    adornos() {
      return this.$store.state.orden.adornos;
    },
    complementos() {
      return this.$store.state.orden.complementos;
    },
    get_total() {
      return this.$store.getters.get_total;
    }
  },
  methods: {
    formatCurrency(value) {
      let val = (value / 1).toFixed(2)
      return '$ ' + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    }

  },
};
</script>
