<template>
  <div class="centered">
    <h1>Adornos</h1>
    <div class="cake-box">
      <div class="container" v-for="adorno, index in adornos">
        <img :src="adorno.img_src" :alt="adorno.nombre" class="cake-image"/>
        <div class="nombre">{{ adorno.nombre }}</div>
        <div class="precio">$ {{ adorno.precio }}</div>
        <div class="descripcion">{{ adorno.descripcion }}</div>
        <button type="button" class="button" @click="agregar_adornos(index, adorno.nombre)">Agregar</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Adornos",
  computed: {
    adornos() {
      return this.$store.state.adornos;
    }
  },
  methods: {
    agregar_adornos: function(index, adorno){
      let payload = {
        index: index,
        nombre : adorno
      }
      this.$store.commit('agregar_adornos', payload)

    }
  }
};
</script>
