<template>
  <div class="box-body">
    <div class="acciones">
        <div class="seccion_accion">
          <a class="comprar" @click="show_menu()"> Menu </a>
        </div>
        <div class="seccion_accion">
          <a class="comprar" @click="show_adornos()"> Adornos </a>
        </div>
        <div class="seccion_accion">
          <a class="comprar" @click="show_complementos()"> Complementos </a>
        </div>
        <div class="seccion_accion">
          <a class="comprar" @click="show_form()"> Comprar <i class="fa-solid fa-cart-shopping"></i> </a>
        </div>
    </div>
    <div id="seccion_menu">
      <Menu />
    </div>
    <div id="seccion_adornos" style="display: none;">
      <Adornos />
    </div>
    <div id="seccion_complementos" style="display: none;">
      <Complementos />
    </div>
    <div id="seccion_formulario" style="display: none;">
      <Formulario/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Menu from "@/components/menu_pasteles.vue";
import Adornos from "@/components/adornos_pasteles.vue";
import Complementos from "@/components/complementos_pasteles.vue";
import Formulario from "@/components/formulario_orden.vue";
import "@/assets/css/estilos_pasteleria.css";
import "@/assets/css/grid_view.css";

export default {
  name: "HomeView",
  components: {
    Menu,
    Adornos,
    Complementos,
    Formulario,
  },
  methods: {
    show_adornos: function () {
      $("#seccion_menu").hide();
      $("#seccion_complementos").hide();
      $("#seccion_formulario").hide();
      $("#seccion_adornos").show();
    },
    show_menu: function () {
      $("#seccion_complementos").hide();
      $("#seccion_adornos").hide();
      $("#seccion_formulario").hide();
      $("#seccion_menu").show();

    },
    show_complementos: function () {
      $("#seccion_menu").hide();
      $("#seccion_adornos").hide();
      $("#seccion_formulario").hide();
      $("#seccion_complementos").show();
    },
    show_form: function() {
      $("#seccion_menu").hide();
      $("#seccion_adornos").hide();
      $("#seccion_complementos").hide();
      $("#seccion_formulario").show();
    }
  },
};
</script>
