<template>
  <div class="centered">
    <h1>Menu</h1>
    <div class="cake-box">
      <div class="container" v-for="pastel, index in sabores">
        <img :src="pastel.img_src" :alt="pastel.nombre" class="cake-image" />
        <div class="nombre">{{ pastel.nombre }}</div>
        <div class="precio">$ {{ pastel.precio }}</div>
        <div class="descripcion">{{ pastel.descripcion }}</div>
        <button type="button" class="button" @click="agregar_pasteles(index, pastel.nombre)">Agregar</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Menu",
  computed: {
    sabores() {
      return this.$store.state.sabores;
    },
  },
  methods: {
    agregar_pasteles: function(index, pastel){
      let payload = {
        index: index,
        nombre : pastel
      }
      this.$store.commit('agregar_pasteles', payload)

    }
  }
};
</script>
